@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  position: relative;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1125px){
      flex-direction: column;
      a {
        margin-bottom: 20px;
      }
      .header__cart{
        display: none;
      }
    }
    @media (max-width: 645px) {
      flex-direction: column;
      .header__cart{
        margin-top: 20px;
      }
    }
  }

  &__logo {
    display: flex;
    justify-content: center;

    img {
      margin-right: 15px;
    }

    h1 {
      margin-top: 4px;
      color: #181818;
      font-size: 24px;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
